import { v4 as uuidv4 } from "uuid";
export function getDataApi(params) {
  let total = 45,
    {page, pageSize } = params,
    content = [],
    len = page * pageSize > total ? total % pageSize : pageSize;
  return new Promise((resolve, reject) => {
    for (let i = 0; i < len; i++) {
      content.push({
        id: i + 1,
        key: i + 1,
        name: "",
        description: "",
        shopId: uuidv4(),
        shopAddress: "",
        shops: "",
        category: ""
      });
    }
    setTimeout(() => {
      resolve({
        flag: true,
        data: content,
        total: total,
      });
    }, Math.random() * 3000);
  });
}
export function addDataApi() {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve();
    }, Math.random() * 3000);
  });
}
